<template>
    <div class="personal_download_layer">
        <header class="login-header-bar clearfix">
            <a onclick="window.history.go(-1);" class="login-back-btn"></a>
            <h2 class="login-title">已下载</h2>
        </header>
      <scroller
        :on-refresh="refresh"
        :on-infinite="infinite"
        ref="my_scroller"

      >
        <div class="download-content-box" v-show="!isShowNoData">
            <ul>
              <li class="download-item" v-for="file in downLoadData">
                <router-link  :to="'/datajifendl/'+file.id"  class="download-item-link">
                  <img v-lazy="file.download_file.file_image" alt="" class="download-item-pic">
                  <div class="download-r">
                    <p class="download-title">
                      {{file.download_file.file_name}}
                    </p>
                    <p class="download-content">
                      {{file.download_file.abstract}}
                    </p>
                    <div class="download-time-box">
                            <span class="download-time">
                                <span>{{file.downloaded_at | formatTime}}</span>    磁易购    来自标签：<span v-for="tag in newsTags">{{tag.name}}</span>
                            </span>
                    </div>
                  </div>
                </router-link>
              </li>
            </ul>
        </div>
      </scroller>
      <NoData v-show="isShowNoData"></NoData>
    </div>
</template>
<script>
    import Api from '../../../api/api';
    import {getDownloadInfo} from '../../../api/pcendownloaded';
    import NoData from '../../nodata/NoData.vue';
    import logUtil from '../../../utils/log'
    export default{
        components:{
          NoData
        },
        data(){
          return{
            isShowNoData:false,
            downLoadData:[],
            newsTags:[],
            //总页数
            totalCount:0,
            //第一页
            pageIndex:1,
            //每一页的数量
            pageSize:10,
            type:0,
          }
        },
        mounted(){
          this.$nextTick(function () {
//            this.$refs.my_scroller.finishInfinite(true);
            this.getDownLoadData(0,1);
          });
        },
      //自定义过滤器
      filters:{
        /**
         * time filter:just get the format of year-month-day
         * */
        formatTime(timeStr){
          const strArry = timeStr.split('T');
          return strArry[0];
        }
      },
        methods:{
          refresh(done){
            const that = this;
            that.pageIndex = 1;
            that.type = 1;
            setTimeout(() => {
              that.getDownLoadData(that.type,that.pageIndex);
              done();
            }, 1500)
          },
          infinite(done) {
            const that = this;
            that.pageIndex++;
            that.type = 2;
            setTimeout(() => {
              //当前页数与每页记录数的乘机大于总记录数就不在加载更多
              if(that.pageIndex>=that.totalCount){
                done(true);
                return;
              }
              done();
              that.getDownLoadData(that.type,that.pageIndex);
            }, 1500)
          },
          /**
           * 获取到下载的资料库的信息
           */
            getDownLoadData(type,page){
              const _this = this;
              getDownloadInfo(`${Api.MYTIEZI}${localStorage.getItem('userId')}/downloads/`,page,_this.pageSize).then((res)=>{
                  _this.totalCount = res.data.data.total;
                  if(res.status === 200 && res.data.data!==null){
                    logUtil.printLog("download success:",res);
                    let tempArr = [];
                    tempArr = res.data.data.detail;
                    if(type===0){
                      _this.downLoadData = tempArr;
                    }
                    //下拉刷新
                    else if(type===1){
                      _this.downLoadData=tempArr.splice(0,_this.pageSize);
                    }else if(type === 2){ //上拉加载
                      _this.downLoadData = _this.downLoadData.concat(tempArr);
                    }
                    const len = _this.downLoadData.length;
                    for(let i = 0; i<len;i++){
                      _this.newsTags = _this.downLoadData[i].download_file.tags;
                    }

                    len > 0 ? _this.isShowNoData = false : _this.isShowNoData = true;
                  }else{
                    _this.$store.dispatch('hideLoading');
                    _this.isShowNoData = true;
                  }
              },(err)=>{
                logUtil.printLog("download fail:",err);
                 _this.$store.dispatch('hideLoading');
                 _this.isShowNoData = true;
              });
            },
            goDataContent($event,abstract,credit,id,url,uploader,downloadCount){
              this.$router.push({path: `/datajifendl/${id}/${abstract}/${credit}/${uploader}/${downloadCount}`, replace: true});
              localStorage.setItem('url',url);
              $event.preventDefault();
            },

        }
    }
</script>
<style scoped>
    @import "../../../assets/css/pcendownloaded.css";
</style>



